import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as Router, NavLink, Route } from 'react-router-dom';
import Informacije from '../Informacije'

const Kriminalisticki = () => {
  const cardInfo = [
    {
      image: "https://www.shortlist.com/media/images/2019/05/the-best-crime-novel-covers-ever-16-1556673831-bNL6-column-width-inline.jpg",
      title: "Crime (2018)",
      text: "Alix Lambert",
    },
    
    {
      image:
        "https://i.pinimg.com/originals/6a/97/c4/6a97c46f46ef196b917912d73778fda2.jpg",
      title: "In at the death (2013)",
      text: "Francis Dunkan",
    },
    {
      image:
        "https://m.media-amazon.com/images/I/51nc1sHZCJL.jpg",
      title: "Irish crime (2012)",
      text: "Roger Harrington",
    },
    {
      image:
        "https://www.shortlist.com/media/images/2019/05/the-best-crime-novel-covers-ever-13-1556673829-GWZL-column-width-inline.jpg",
      title: "The big sleep (2005)",
      text: "Raymond Chandlers",
    },
    {
      image:
        "https://m.media-amazon.com/images/I/41SqYpbLaLL.jpg",
      title: "Criminal law (2014)",
      text: "Mohit Purry",
    },
    
];

  const renderCard = (card, index) => {
    return (
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          <Button path='/informacije' exact component={Informacije} variant="primary">Detalji</Button>
          <Router>
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          </Router>
        </Card.Body>
      </Card>
    );
  };

  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Kriminalisticki;