import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as Router, NavLink, Route } from 'react-router-dom';
import Informacije from '../Informacije'

const Tinejdz = () => {
  const cardInfo = [
    {
      image: "https://www.designwizard.com/wp-content/uploads/2019/06/faea2f50-8791-11e9-a06c-4b8c22314c8b.jpg",
      title: "Dream Chester (2012)",
      text: "Joan Smith",
    },
    
    {
      image:
        "https://www.designwizard.com/wp-content/uploads/2019/06/d7077da0-8795-11e9-b73b-27b286df2c8e.jpg",
      title: "Wild flowers of poesy (2016)",
      text: "Joan Smith ",
    },
    {
      image:
        "https://i.pinimg.com/236x/7d/f2/12/7df212e6588a700547b1923757405f9e--vanilla-book-covers.jpg",
      title: "Vanilla (2015)",
      text: "Billi Merrel",
    },
    {
      image:
        "https://fionajaydemedia.com/wp-content/uploads/2017/04/VanishedFinal-FJM_Low_Res_500x750.jpg",
      title: "Vanished (2016)",
      text: "Eric MEcCOLE",
    },
    {
      image:
        "https://i.pinimg.com/236x/4d/71/89/4d7189e490a248f8ec4f4fe939562af8.jpg",
      title: "The hate you give (2010)",
      text: "Anigie Thomas",
    },
    
];

  const renderCard = (card, index) => {
    return (
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          <Button variant="primary">Detalji</Button>
          <Router>
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          </Router>
        </Card.Body>
      </Card>
    );
  };

  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Tinejdz;