import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as Router, NavLink, Route } from 'react-router-dom';
import Informacije from '../Informacije'

const Poezija = () => {
  const cardInfo = [
    {
      image: "https://images-na.ssl-images-amazon.com/images/I/71DUF9JrAmL.jpg",
      title: "The Wing of Poesy  (2010)",
      text: "Shv Shankar",
    },
    
    {
      image:
        "https://images-na.ssl-images-amazon.com/images/I/41z3OEM99gL._SX331_BO1,204,203,200_.jpg",
      title: "Wild flowers of poesy (2016)",
      text: "Joseph Buttler ",
    },
    {
      image:
        "https://images-na.ssl-images-amazon.com/images/I/81kWJHfOxWL.__BG0,0,0,0_FMpng_AC_UL600_SR378,600_.jpg",
      title: "Celestial days (2010)",
      text: "B.D Benedikt",
    },
    {
      image:
        "https://notionpress.com/thumbs/phpThumb.php?src=../coveruploads/21681_frontcover_Coverapp_image_224368_1.png&f=png&h=380",
      title: "Poesy of Illusion (2015)",
      text: "Hasan Kazmi",
    },
    {
      image:
        "https://www.mydomaine.com/thmb/XDsdM6viq9uYJWGzU34RYRmBmxc=/700x796/filters:no_upscale():max_bytes(150000):strip_icc()/cdn.cliqueinc.com__cache__posts__274290__love-poem-books-274290-1544048394187-product.700x0c-b968757dc20c4d5fa55e562fe81c28c9.jpg",
      title: "Milk and honey (2009)",
      text: "Rupi Caur",
    },
    
];

  const renderCard = (card, index) => {
    return (
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          <Button path='/informacije' exact component={Informacije} variant="primary">Detalji</Button>
          <Router>
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          </Router>
        </Card.Body>
      </Card>
    );
  };

  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Poezija;