import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as Router, NavLink, Route } from 'react-router-dom';
import Informacije from '../Informacije'

const Deca = () => {
  const cardInfo = [
    {
      image: "https://99designs-blog.imgix.net/blog/wp-content/uploads/2017/07/attachment_73599840-e1500060411553.png?auto=format&q=60&fit=max&w=930",
      title: "In your own backyard (2017)",
      text: "A.M. Khalifa",
    },
    
    {
      image:
        "https://blog-cdn.reedsy.com/uploads/2019/12/stargazing-705x1024.jpg",
      title: "Stargazing (2018)",
      text: "Jen Wang ",
    },
    {
      image:
        "https://i.pinimg.com/originals/28/22/cf/2822cf4c154d4ca6a26bf65107750b35.jpg",
      title: "Sam and Pam",
      text: "Iris Garde",
    },
    {
      image:
        "https://lh3.googleusercontent.com/proxy/3Ci1SEAafVeu_0-PNeFPARnf-VnAhm2zwe_5BHdiRXA0QXwUJoqmyQuLXE87mbENFYnEIJWskNnqqQdHzzOnsrF4OiH43QvxyIw1Wygh7ejUuwG6qszCf0Et4g",
      title: "The cat in the hat",
      text: "Dr Seuss",
    },
    {
      image:
        "https://d2ct9xspam8wud.cloudfront.net/blog/2020/07/10152840/A-Kite-for-the-Moon.jpg",
      title: "Akite for moon",
      text: "Jane Jolen",
    },
    
  
   
   
   
    
    
   
    
    
    
   
    
    
    
    
  ];

  const renderCard = (card, index) => {
    return (
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          <Button path='/informacije'  variant="primary">Detalji</Button>
          <Router>
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          </Router>
        </Card.Body>
         
      </Card>
    );
  };

  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Deca;