import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as Router, NavLink, Route } from 'react-router-dom';
import Informacije from '../Informacije'

const Horor = () => {
  const cardInfo = [
    {
      image: "https://images4.penguinrandomhouse.com/cover/9781616951009",
      title: "Come closer (2010)",
      text: "Sara Gran",
    },
    
    {
      image:
        "https://i.pinimg.com/236x/07/4a/b8/074ab854fee1e9cf94419721a0910f50--horror-books-horror-art.jpg",
      title: "Death true (2017)",
      text: "David J. Michael ",
    },
    {
      image:
        "https://i.pinimg.com/originals/35/c3/90/35c3907496cdc951ddf56087c3b57dd3.jpg",
      title: "Daddy little's girl(2000)",
      text: "Daniel Ronsom",
    },
    {
      image:
        "https://i.pinimg.com/originals/7d/fa/54/7dfa547fa4789b0e03f0b76448794128.jpg",
      title: "Nightmares (2015)",
      text: "Andy Graham",
    },
    {
      image:
        "https://ml3djpgzrp83.i.optimole.com/0x4yz3Y-g_ZOoXMD/w:auto/h:auto/q:auto/https://miblart.com/wp-content/uploads/2020/01/fantasy.png",
      title: "Nightmare (2003)",
      text: "Elen Datlow",
    },
    
];

  const renderCard = (card, index) => {
    return (
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          <Button path='/informacije' exact component={Informacije} variant="primary">Detalji</Button>
          <Router>
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          </Router>
        </Card.Body>
      </Card>
    );
  };

  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Horor;