import React, { useState } from 'react';
import { Button } from './Button';
import { Link } from 'react-router-dom';
import './Navbar.css';
import Dropdown from './Dropdown';
import Sidebar from './Sidebar'
import Stranicakategorije from './pages/Stranicakategorije'



function Navbar() {
     const [click, setClick]= useState(false);
    const [dropdown, setDropdown] = useState(false);

    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);

    const onMouseEnter = () => {
        if(window.innerWidth < 960) {
            setDropdown(false);
        } else {
            setDropdown(true);
        }
    };
  
    const onMouseLeave = () => {
        if(window.innerWidth < 960) {
            setDropdown(false);
        } else {
            setDropdown(false);
        }
    };



    
    return (
        <>
        
        <nav className='navbar'>
           
        <Sidebar />
        
            
            <Link to='/'className='navbar-logo'>
            

            BOOKSELLER <i className='fab fa-firstdraft' />
            </Link>
            
            <div className="spacer" />
            <div classname='menu-icon' onClick={handleClick}>
               
                

             </div>
            
            <ul className={click ? 'nav-menuu active' : 'nav-menuu'}>
                <li className='nav-item'>
                    
                </li>
                <li className='nav-item'
                    onMouseEnter={onMouseEnter}
                    onMouseLeave={onMouseLeave}
                    ></li>
                    
                 <li className='nav-item'>
                     <Link to='/administracije' className='nav-links'
                     onClick={closeMobileMenu}>
                         Administracije
                     </Link>
                     </li>
                     <li className='nav-item'>
                     <Link to='/informacije' className='nav-links'
                     onClick={closeMobileMenu}>
                         Informacije
                     </Link>
                     </li>
            </ul>
            {/* <Button />  */}
        </nav>
        </>
    );
}

export default Navbar;