import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as Router, NavLink, Route } from 'react-router-dom';
 import Informacije from '../Informacije'

const Triler = () => {
  const cardInfo = [
    {
      image: "https://www.mysterytribune.com/wp-content/uploads/2017/12/Terminal-Rage-am-khalifa-best-mystery-thriller-book-covers-2017-677x1024.jpg",
      title: "Terminal Rage (2017)",
      text: "A.M. Khalifa",
    },
    
    {
      image:
        "https://i.pinimg.com/originals/3e/3a/bd/3e3abdc0dba7f1c893f2dec8b9617bd3.jpg",
      title: "Truth or Dare (2018)",
      text: "Jeff Wadlow ",
    },
    {
      image:
        "https://images.squarespace-cdn.com/content/v1/5202d1b3e4b099a0812c51a3/1483134080936-K240V6OPPWM2J7NY8SE8/ke17ZwdGBToddI8pDm48kFjiq_0Ek1NItql1dLmsgNNZw-zPPgdn4jUwVcJE1ZvWEtT5uBSRWt4vQZAgTJucoTqqXjS3CfNDSuuf31e0tVGQiKALUW-YxfFfP4p0PvHcTla7NnOOvQwSGt9YLKLGBhEym7bB5Y8EAaW-uC2HxJQ/Blood-Stained.jpg",
      title: "Blood Stain",
      text: "Peter Lalor",
    },
    {
      image:
        "https://i.pinimg.com/originals/7c/45/08/7c45083e837d5c2a52a329f525d8b613.jpg",
      title: "The fields of daylight",
      text: "Tristan Hopkins",
    },
    {
      image:
        "https://images.squarespace-cdn.com/content/v1/5202d1b3e4b099a0812c51a3/1483134082559-JW2JYU3NT5YVALKHWQFB/ke17ZwdGBToddI8pDm48kFjiq_0Ek1NItql1dLmsgNNZw-zPPgdn4jUwVcJE1ZvWEtT5uBSRWt4vQZAgTJucoTqqXjS3CfNDSuuf31e0tVGQiKALUW-YxfFfP4p0PvHcTla7NnOOvQwSGt9YLKLGBhEym7bB5Y8EAaW-uC2HxJQ/Snake-Skin.jpg",
      title: "Snake skin",
      text: "CJ Lyons",
    },
    
  
   
   
   
    
    
   
    
    
    
   
    
    
    
    
  ];
  

  const renderCard = (card, index) => {
    return (
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          
      
          <Button path='/informacije' exact component={Informacije} variant="primary">Detalji</Button>
          <Router>
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          </Router>
        </Card.Body>
      </Card>
      
    );
  };

  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Triler;