import React, { Component } from "react";
import './Home.css'
import { Route, NavLink, HashRouter } from "react-router-dom";
import Triler from './zanrovi/Triler';
import Ljubavni from './zanrovi/Ljubavni';
import Kriminalisticki from "./zanrovi/Kriminalisticki";
import Istorijski from './zanrovi/Istorijski';
import Horor from './zanrovi/Horor';
import Fantazija from './zanrovi/Fantazija';
import Akcioni from './zanrovi/Akcioni';
import Deca from './zanrovi/Deca';
import Naucna from './zanrovi/Naucna';
import Tinejdz from './zanrovi/Tinejdz';
import Poezija from './zanrovi/Poezija';
import Komedija from './zanrovi/Komedija';
import FilteredList from './FilteredList';




 
class Main extends Component {
  render() {
    return (
      <HashRouter>
        <div>
        <div id="mount-point"></div>
          <h3 className='naslov'>ŽANROVI KNJIGA</h3>
          
          <ul className="header">
          
            <li><NavLink className='nav-links'to="/triler">Triler</NavLink></li> 
            <li><NavLink className='nav-links' to="/ljubavni">Ljubavni</NavLink></li>
            <li><NavLink className='nav-links' to="/kriminalisticki">Kriminalisticki</NavLink></li>
            <li><NavLink className='nav-links' to="/istorijski">Istorijski</NavLink></li>
            <li><NavLink className='nav-links' to="/horor">Horor</NavLink></li>
            <li><NavLink className='nav-links' to="/fantazija">Fantazija</NavLink></li>
            <li><NavLink className='nav-links' to="/poezija">Poezija</NavLink></li>
            <li><NavLink className='nav-links' to="/naucna">Naucna fantastika</NavLink></li>
            
            <li><NavLink className='nav-links' to="/komedija">Komedija</NavLink></li>
            <li><NavLink className='nav-links' to="/tinejdz">Tinejdz</NavLink></li>
            <li><NavLink className='nav-links' to="/deca">Knjige za decu</NavLink></li>
            <li><NavLink className='nav-links' to="/akcioni">Akcioni</NavLink></li>
            
          </ul>
          
          
          <div className="content">
          
          <Route path="/triler" component={Triler}/>
          <Route path="/ljubavni" component={Ljubavni}/>
          <Route path="/kriminalisticki" component={Kriminalisticki}/>
          <Route path="/istorijski" component={Istorijski}/>
          <Route path="/horor" component={Horor}/>
          <Route path="/fantazija" component={Fantazija}/>
          <Route path="/poezija" component={Poezija}/>
          <Route path="/naucna" component={Naucna}/>
         
          <Route path="/komedija" component={Komedija}/>
          <Route path="/tinejdz" component={Tinejdz}/>
          <Route path="/deca" component={Deca}/>
          <Route path="/akcioni" component={Akcioni}/>
          <FilteredList />
          
          </div>
        </div>
        </HashRouter>
    );
  }
}
 
export default Main;

 
