import React from 'react'

function Informacije() {
    return (
        
            <div class="wrap">
    <h2>Sta je React?</h2>
    <p class='class="underlined underlined--thin"'> React je JavaScript biblioteka kreirana sa
     ciljem izgradnje korisničkih interfejsa.
     Stvorio ga je Jordan Valke, programer u kompaniji Fejsbuk. Od svog debija 2011. godine,
      React je postao jedan od najpopularnijih front-end framework-a za izradu veb aplikacija
      sa ogromnom zajednicom pojedinačnih programera i kompanija.</p>
  
  <h2>Sta je JSX</h2>
 <p> JSX odnosno JavaScript XML, predstavlja JavaScript sintaksnu ekstenziju.
      JSX nam omogucava da zajedno koristimo HTML i JavaScript. React komponente
       se uglavnom pisu koristeci JSX, ali nije obavezno. JSX konvertuje HTML tagove
        u react elemente.</p>
  
  <h2>Navedi neke od glavnih prednosti React-a?</h2>
 <p> React omogućava laku tranziciju stranica bez potrebe za ponovnim učitavanjem,
      čineći aplikacije brzim, skalabilnim (što se tiče komponenata za višekratnu upotrebu)
       i pojednostavljenim. U ovom odeljku pokrićemo ostale važne karakteristike koje čine
        React tako neizmerno popularnim među zajednicom programera.</p>
  
  <h2>Sta je virtualni DOM?</h2>
  <p>React ima kontrolu nad DOM elementima tako što pravi JS objekat za svaki HTML 
      element koji želimo da se prikaže na ekranu. Taj JS objekat može da predstavlja
       element ili komponentu, koji u sebi mogu da sadrže druge elemente ili komponente.
        Na taj način React pravi svoj Virtualni DOM koji se kasnije koristi za upoređivanje
         i updateovanje HTML DOM-a koji browser koristi za renderovanje stranice.</p>
   
  
  <h2>Sta je state a sta props? Koja je razlika?</h2>
 <p> Vrednostima za stanja (state) i osobine (props) react vrši sva upravljanja u svojim aplikacijama.
      Stanja su promenjive vrednosti koje mogu biti postavljene golobalno: kao sadržaji kosntaniti ili promenjivih
       ili kao pročitani sadržaji iz baze podataka, ili lokalno : kao sadržaji kosntaniti ili promenjivih</p>

 <h2>Postoje 3 različite faze životnog ciklusa komponente u React-u. Koje su to?</h2>
 <p>Render, componentDidMount, componentDidUpdate</p>



 <h2>Koja je razlika izmedju kontrolisanih i nekontrolisanih komponenti?</h2>
 <p>DOM elementi za unos u obrascima, imaju vrijednost koju korisnik može mijenjati.
Elementi za unos mogu biti kontrolisani i nekontrolisani.</p>


<h2>Šta su Higher Order Components(HOC)?</h2>
<p>To je napredna tehnologija u React-u</p>

<h2>Šta su Pure Components?</h2>
<p>Pure components ne zavise i ne mjenjaju stanje promjenjivih izvan svog opsega. </p>

<h2>Šta je React Router?</h2>
<p>U tradicionalnim web stranicama, u adresnoj traci preglednika korisnik može provjeriti
na kojoj se stranici nalazi. Kod jednostraničnih web aplikacija, moguće su potpune izmjene
stranice bez da se promijeni sadržaj adresne trake, te korisnik ne može koristiti tipke naprijed
i natrag za navigaciju. Kako je ovaj problem moguće riješiti u JavaScriptu, napravljena je
biblioteka koja to rješava i nazvana je Reacr Router. Kao i React, sastoji se od dva dijela,
React Router i React Router DOM. Biblioteka je deklarativna poput Reacta, i koristi se
identično kao i komponente.</p>

<h2>Šta su styled components?</h2>
<p>CSS styled components je jezik pomoću kojeg se opisuje izgled HTML
dokumenta u pregledniku. Važno je napomenuti kako CSS ne može nadomjestiti semantiku,
pa iako stranica može izgledati isto korištenjem CSS-a, važno je upotrijebiti odgovarajuće
HTML oznake za opis sadržaja.</p>

<h2>Šta su React Hooks?</h2>
<p>React Hooks omogucava da se ukljucimo u 'hook into' i funkciju zivotnog ciklusa iz function components</p>



</div>

        
    )
}

export default Informacije
