import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as Router, NavLink, Route } from 'react-router-dom';
import Informacije from '../Informacije'

const Istorijski = () => {
  const cardInfo = [
    {
      image: "https://i.pinimg.com/236x/b5/fd/4e/b5fd4eff1823a653e41a571b6d2693cc--novel.jpg",
      title: "Window walk (2000)",
      text: "Sara Gran",
    },
    
    {
      image:
        "https://www.e-elgar.com/shop/media/catalog/product/9/7/9781847209375.jpg",
      title: "Modern China (2012)",
      text: "Joshep C.G Chai",
    },
    {
      image:
        "https://99designs-blog.imgix.net/blog/wp-content/uploads/2017/02/attachment_70356173-e1488218242409.jpg?auto=format&q=60&fit=max&w=930",
      title: "Red planet (2004)",
      text: "Robert Henglein",
    },
    {
      image:
        "https://damonza.com/wp-content/uploads/portfolio/fiction/torrent-15.jpg",
      title: "Torrent (2015)",
      text: "David Meyer",
    },
    {
      image:
        "https://productimages.worldofbooks.com/186064208X.jpg",
      title: "Modern British history (2009)",
      text: "L.J Buttler",
    },
    
];

  const renderCard = (card, index) => {
    return (
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          <Button path='/informacije' exact component={Informacije} variant="primary">Detalji</Button>
          <Router>
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          </Router>
        </Card.Body>
      </Card>
    );
  };

  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Istorijski;