import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as Router, NavLink, Route } from 'react-router-dom';
import Informacije from '../Informacije'

const Fantazija = (image, title, text) => {
  const cardInfo = [
    {
      image: "https://99designs-blog.imgix.net/blog/wp-content/uploads/2017/02/41DNuJfahyL.jpg?auto=format&q=60&fit=max&w=930",
      title: "The Martin  (2020)",
      text: "Andy Weir",
    },
   
    
    {
      image:
        "https://i.pinimg.com/236x/b9/7a/df/b97adf74fcf59ea02158a8bbfaa3740e.jpg",
      title: "Blood Less (2011)",
      text: "April Mccloi ",
    },
    {
      image:
        "https://images-na.ssl-images-amazon.com/images/I/510Jpbpv2uL._SX331_BO1,204,203,200_.jpg",
      title: "Paralelni svet (2010)",
      text: "B.D Benedikt",
    },
    {
      image:
        "https://ml3djpgzrp83.i.optimole.com/0x4yz3Y-CEOGHaTV/w:683/h:1024/q:auto/https://miblart.com/wp-content/uploads/2020/01/thriller-cover.jpeg",
      title: "An angel fallen (2015)",
      text: "Andy Graham",
    },
    {
      image:
        "https://ml3djpgzrp83.i.optimole.com/0x4yz3Y-g_ZOoXMD/w:auto/h:auto/q:auto/https://miblart.com/wp-content/uploads/2020/01/fantasy.png",
      title: "Fate Dragon (2009)",
      text: "Alisha Klaplahe",
    },
    
];


  const renderCard = (card, index) => {
    return (
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          <Button path='/informacije' exact component={Informacije} variant="primary">Detalji</Button>
          <Router>
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          
          </Router>
        </Card.Body>
      </Card>
      
    );
  };
  

  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Fantazija;