import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as HashRouter, NavLink, Route } from 'react-router-dom';
import Informacije from '../Informacije'

const Akcioni = () => {
  const cardInfo = [
    {
      image: "https://i.pinimg.com/originals/1e/7f/8f/1e7f8f5b27bbbfc3d20fb914f0bf9eb3.jpg",
      title: "Test of revenge (2017)",
      text: "Jessica Gleave",
    },
    
    
    {
      image:
        "https://i.pinimg.com/736x/ee/5d/80/ee5d8057e8f215be2d5718e8fef9db7d.jpg",
      title: "Charon Docks at Daylight (2012)",
      text: "Zoe Red ",
    },
    {
      image:
        "https://www.bookcoversart.com/wp-content/uploads/2017/05/The-House-in-the-Fog-Premade-Action-Thriller-Mystery-Horror-Crime-Spy-Detective-Story-eBook-Book-Cover.jpg",
      title: "The house in the fog",
      text: "John J. Smith",
    },
    {
      image:
        "https://i.pinimg.com/originals/a9/b5/99/a9b599a86b0e9d266cbcc05eb39427ee.jpg",
      title: "The serial seven",
      text: "J.D. Cavan",
    },
    {
      image:
        "https://i.pinimg.com/originals/8c/7e/85/8c7e85c04c2339559526ea95652b2701.jpg",
      title: "Hope's promise",
      text: "G.P. Ching",
    },
    
];

  const renderCard = (card, index) => {
    return (
      <HashRouter>
        
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          <Button path='/informacije' exact component={Informacije} variant="primary">Detalji</Button>
          
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          

        </Card.Body>
      </Card>
      </HashRouter>
      
    );
  };
  
  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Akcioni;