import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as Router, NavLink, Route } from 'react-router-dom';
import Informacije from '../Informacije'

const Komedija = () => {
  const cardInfo = [
    {
      image: "https://m.media-amazon.com/images/I/41J6NowLqAL.jpg",
      title: "Hero's journey (2008)",
      text: "Steve Caplan",
    },
    
    {
      image:
        "https://m.media-amazon.com/images/I/5122eRyfcrL.jpg",
      title: "The best medicine (2012)",
      text: "F. Charlote",
    },
    {
      image:
        "https://res.cloudinary.com/bookbub/image/upload/t_ci_ar_6:9_scaled,f_auto,q_auto,dpr_1/v1532279987/pro_pbid_3610917.jpg",
      title: "Elenaor Oplihant (2014)",
      text: "Gally Honeyman",
    },
    {
      image:
        "https://www.jonstubbington.com/wp-content/uploads/2019/07/Book-cover-illustration-comedy-calvin-reads-the-bible.jpg",
      title: "Calvin reads a Bible (2005)",
      text: "Donie Herron",
    },
    {
      image:
        "https://coveryourdreams.files.wordpress.com/2013/07/ditched12-copy.jpg",
      title: "Ditched again (2009)",
      text: "Stacey Yoj",
    },
    
];

  const renderCard = (card, index) => {
    return (
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          <Button path='/informacije' exact component={Informacije} variant="primary">Detalji</Button>
          <Router>
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          </Router>
        </Card.Body>
      </Card>
    );
  };

  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Komedija;