import React from "react";
import "./Box.css";
import { Card, Button } from "react-bootstrap";
import { BrowserRouter as Router, NavLink, Route } from 'react-router-dom';
import Informacije from '../Informacije'

const Ljubavni = () => {
  const cardInfo = [
    {
      image: "https://thebookcoverdesigner.com/wp-content/uploads/2019/11/Love-Story.jpg",
      title: "Love Story (2020)",
      text: "Alix Lambert",
    },
    
    {
      image:
        "https://d1csarkz8obe9u.cloudfront.net/posterpreviews/romantic-love-story-book-cover-design-template-a1188e523cb9f6495f3c022a1d9d2928_screen.jpg?ts=1588747775",
      title: "Speak (2016)",
      text: "Anna Wright",
    },
    {
      image:
        "https://www.creativeparamita.com/wp-content/uploads/2018/09/we-still-believe-in-love.jpg",
      title: "We still belive in love (2016)",
      text: "Roger Harrington",
    },
    {
      image:
        "https://i.pinimg.com/236x/0c/ee/7e/0cee7e54fda8ac99ec11459448e89c7d.jpg",
      title: "No place like here (2009)",
      text: "Christina June",
    },
    {
      image:
        "https://images3.penguinrandomhouse.com/cover/9780735218314",
      title: "More then words (2014)",
      text: "Jil Santopolo",
    },
    
];

  const renderCard = (card, index) => {
    return (
      <Card style={{ width: "18rem" }} key={index} className="box">
        <Card.Img variant="top" src="holder.js/100px180" src={card.image} />
        <Card.Body>
          <Card.Title>{card.title}</Card.Title>
          <Card.Text>{card.text}</Card.Text>
          <Button path='/informacije' exact component={Informacije} variant="primary">Detalji</Button>
          <Router>
          <li><NavLink className='nav-links'to="/informacije">Informacije</NavLink></li>
          <Route path='/informacije' exact component={Informacije} />
          </Router>
        </Card.Body>
      </Card>
    );
  };

  return <div className="grid">{cardInfo.map(renderCard)}</div>;
};

export default Ljubavni;